/* src/AboutUs.css */
.about-us {
  font-family: Arial, sans-serif;
  padding: 20px;
  margin: 80px auto;
  max-width: 70vw;
  background-color: transparent;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.about-us h1 {
  text-align: center;
}

.about-us h2 {
  margin-top: 20px;
}

.about-us p {
  line-height: 1.6;
}
